.n-footer
   font-family: 'Montserrat', sans-serif
   background: #061429
   padding: 70px 0
   color: #ffffff
   &-row
      display: flex
      flex-direction: row
      justify-content: space-between
      &--border
         justify-content: start
         align-items: center
         margin-top: 29px
         padding: 19px 0
         border-top: 2px solid rgba(255, 255, 255, 0.13)
         border-bottom: 2px solid rgba(255, 255, 255, 0.13)
   &-title
      font-weight: 600
      font-size: 25px
      line-height: 1.37
      max-width: 228px
   &-btn
      text-align: center
      display: inline-block
      margin-top: 28px
      padding: 11px
      min-width: 190px
      font-weight: 700
      font-size: 16px
      line-height: 137.5%
      color: #060C29
      background: #ffffff
      border-radius: 41px
      transition: all .3s linear
      &:hover
         color: rgba(6, 12, 41, 0.5)
      &--login
         background-color: transparent
         border: 1px solid #ffffff
         color: #ffffff
         &:hover
            color: rgba(255, 255, 255, 0.5)
   &__col
      width: calc((100% - 270px) / 4)
      &_title
         font-weight: 600
         margin-bottom: 15px
         font-size: 24px
         line-height: 30px
         color: rgba(120, 202, 255, 0.48)
         &:not(:first-child)
            margin-top: 20px
      ul
         li
            display: block
            a
               font-weight: 600
               line-height: 42px
               font-size: 12px
               color: #FFFFFF
               transition: all .3s linear
               &:hover
                  color: rgba(255, 255, 255, 0.58)
   &-logo
      width: 26%
      & .dark
         display: none
   &-social
      margin-right: 46px
      display: flex
      &__link
         margin-right: 17px
         transition: all .3s linear
         display: block
         &:hover
            transform: scale(1.15)
         & .dark
            display: none
   &-docs
      display: flex
      flex-direction: column
      &__link
         font-size: 10px
         line-height: 1.57
         color: #ffffff
         &:hover
            color: rgba(255, 255, 255, 0.5)
   &-copyright
      flex: 1 0 auto
      text-align: right
      padding-left: 5px
      font-weight: 500
      font-size: 14px
      line-height: 1.2

@media screen and (max-width: 1400px) and (min-width: 1201px)
   .n-footer__col
      width: calc((100% - 200px) / 4)

@media screen and (max-width: 1200px)
   .n-footer
      padding: 34px 18px
      & .container
         max-width: initial
@media screen and (max-width: 1023px)
   .n-footer
      padding: 34px 18px 95px
      position: relative
      &-copyright
         position: absolute
         bottom: 34px
         right: 50%
         transform: translateX(50%)
      &-row
         justify-content: space-between
      &__col
         flex: 1 1
         width: auto
         padding-right: 28px
         &:last-child
            padding-right: 0
      &__col_title
         font-size: 20px
      & .container
         max-width: initial
         padding: 0

@media screen and (max-width: 768px)
   .n-footer
      padding: 32px 0 94px
      &-row
         flex-direction: column
         justify-content: center
         padding: 0 24px
         &--border
            justify-content: space-between
            padding: 24px
            flex-wrap: wrap
            flex-direction: row
      &__col
         width: 100%
         margin-bottom: 26px
         display: flex
         flex-direction: column
         align-items: flex-start
         @include smalltablet
            padding-right: 0
         &:first-child
            display: none
         &_title
            margin-bottom: 9px
            line-height: 32px
            cursor: pointer
            position: relative
            padding-right: 99px
            @include smalltablet
               width: 100%
               width: -moz-available
               width: -webkit-fill-available
               width: fill-available
            br
               display: none
            &::after
               content: ''
               position: absolute
               width: 24px
               height: 100%
               right: 0
               top: 0
               background: url('../img/main/footer-chevron.svg') center no-repeat
               transform: rotate(180deg)
               transition: .2s
               color: inherit
            &.open
               &::after
                  transform: rotate(0)
               & + ul
                  display: block
            &:not(.accordion).open
               br
                  display: block
         ul
            display: none
      &-social
         margin-right: 0
         &__link:last-child
            margin-right: 0
      &-docs
         width: 100%
         margin-top: 16px
      &-copyright
         width: 100%
         text-align: center

.light .n-footer
   color: #061429
   background: #E8F6F9
   &-row--border
      border-top: 2px solid rgba(6, 20, 41, 0.13)
      border-bottom: 2px solid rgba(6, 20, 41, 0.13)
   & .n-footer__col_title
      color: rgba(120, 202, 255, 0.74)
   &-logo
      & img
         display: none
      & .dark
         display: block
   &-title
      color: #061429
   &-social
      &__link
         & img
            display: none
         & .dark
            display: block
   &-docs
      &__link
         color: #061429
   &-btn
      color: #ffffff
      background: #060C29
      &:hover
         color: rgba(255, 255, 255, 0.5)
      &--login
         background-color: transparent
         border: 1px solid #060C29
         color: #060C29
         &:hover
            color: rgba(6, 12, 41, 0.5)
