.breadcrumbs
    list-style-type: none
    display: flex
    align-items: center
    padding: 0
    color: rgba(255, 255, 255, 0.50)
    & li
        color: $white
        &:not(:last-child)
            color: rgba(255, 255, 255, 0.50)
            position: relative
            &::after
                content: '/'
                margin: 0 8px
        & a 
            color: inherit
