.n-header
  font-family: 'Montserrat', sans-serif
  padding: 16px 0
  position: relative
  .container
    padding: 0 16px
    max-width: 100%
    display: flex
    justify-content: space-between
    align-items: center
  &__main-link
    position: relative
    z-index: 2
    cursor: pointer
    &.winter
      align-self: center
      & img
        width: 103px
        height: 21px
    .logo-dark
      display: none
  & .notification
    display: none
  .n-burger-body
    display: flex
    align-items: center
    & .notification
      display: block
    nav
      margin: auto 0
    ul
      display: flex
      margin-right: 96px
      li
        display: block
        margin-right: 40px
        &.n-burger-item
          display: none
        &:last-child
          margin-right: 0
        a
          font-weight: 600
          font-size: 14px
          line-height: 24px
          color: #FFFFFF
          transition: .1s
          &:hover
            color: rgba(255, 255, 255, 0.5)  
    & .n-burger__notification
      display: flex
      justify-content: space-between
      align-items: center
      color: #fff
    & .n-burger__notification-count
      background: #ED2661
      font-weight: 400
      font-size: 10px
      text-align: center
      line-height: 1
      min-width: 16px
      border-radius: 50%
      padding: 3px 5px
      color: #fff
    & .n-burger__lang
      display: flex
      justify-content: space-between
      align-items: center
      color: #fff
      position: relative
      &-select
        flex: 1 0 auto
        text-align: right
        &.select-show .select-dropdown 
          transform: translate(0)
          bottom: 0
          top: auto
      & .select-toggle
        position: absolute
        top: 0
        left: 0
        color: #fff
        background: none
        border: none
        outline: none
        width: 100%
        text-align: right
      & .select-dropdown
        text-align: left
        flex-direction: column
        position: fixed
        z-index: 10000
        top: 0
        right: 0
        background: linear-gradient(97.39deg, #142546 0.62%, #142341 100%)
        padding: 20px
        width: 360px
        max-width: 100%
        height: 100vh
        box-sizing: border-box
        border-radius: 0 0 0 8px
        transform: translate(0, -100%)
        transition: .35s
      & .select-dropdown-head
        position: relative
        text-align: center
        margin-bottom: 50px
      & .select-dropdown-close
        position: absolute
        top: -5px
        left: 0
        & svg path
          fill: #fff
  &__reg-btn
    padding: 8px 12px
    font-weight: 600
    font-size: 14px
    line-height: 1.21
    background: linear-gradient(90deg, #CDEBFF 0%, #E8EFFF 100%)
    border-radius: 7px
    color: #253962
    transition: all .5s ease-in-out
    margin-left: 0
    margin-right: 15px
    &:hover 
      color: rgba(37, 57, 98, 0.8)
    &--tablet
      display: none
  &__auth-btn
    font-weight: 600
    font-size: 14px
    line-height: 1.22
    color: #FFFFFF
    transition: .1s
    box-sizing: border-box
    margin: 3px 40px 0 0
    transition: all .5s ease-in-out
    &:hover
      color: rgba(255, 255, 255, 0.5)
  &__theme-wrapper
    margin: auto 0
    width: 44px
    height: 34px
    p
      display: none
  &__theme-toggler
    background: none
    border: 0
    margin: auto 0
    cursor: pointer
    svg
      &.dark
        display: none
      &.light
        color: #FFFFFF
  &__burger-auth
    display: none
  .n-burger-btn
    display: none
  &_auth-user
    display: none
  &__burger-logout
    display: none
  .notification 
    position: relative
    margin-right: 20px
    width: 32px
    height: 32px
    cursor: pointer
    background-image: url(../img/notification-icon-w.svg) 
    background-repeat: no-repeat
    background-position: center
    &-number
      position: absolute
      top: 0
      left: 19px
      padding: 1px 3px 0
      min-width: 12px
      height: 12px
      font-size: 10px
      line-height: 1
      text-align: center
      color: #FFFFFF
      border-radius: 12px
      background-color: #ED2661	
  .notification-body
    display: none
    position: absolute
    top: 100%
    left: 0
    width: 235px
    background: #131D32
    border: 1px solid #3D5481
    border-radius: 8px
    overflow: hidden
    color: #fff
    z-index: 100
    &.open
      display: block
    &__header
      display: flex
      align-items: center
      justify-content: space-between
      padding: 12px 8px 8px
      border-bottom: 1px solid #3d5481
    &__count
      font-weight: 600
      font-size: 16px
      line-height: 1.25
      color: #fff
    &__link
      font-weight: 400
      font-size: 12px
      line-height: 15px
      color: #40A7EA
    &__list
      display: flex
      flex-direction: column
      margin: 0!important
      max-height: 295px
      overflow-y: auto
      overflow-x: visible
      -ms-overflow-style: none
        overflow: -moz-scrollbars-none
      &::-webkit-scrollbar
        weight: 0 
        height: 0
      & .simplebar-track.simplebar-vertical 
        width: 7px
        top: 9px
        right: 3px
        pointer-events: auto
      & .simplebar-scrollbar:before
        background: #3D5481
        border-radius: 100px
      & .simplebar-scrollbar.simplebar-visible:before
        opacity: 1
    &__item
      margin: 0!important
      padding: 8px 0 8px 9px
      cursor: default
      &:not(:first-child)
        border-top: 1px solid #3d5481
    &__title
      font-weight: 600
      font-size: 12px
      line-height: 15px
      color: #FFFFFF
      margin-bottom: 4px
    &__text
      font-weight: 400
      font-size: 10px
      line-height: 12px
      color: #FFFFFF
      margin-bottom: 4px
      white-space: nowrap
      max-width: 191px
      overflow: hidden
      text-overflow: ellipsis
    &__time
      font-weight: 400
      font-size: 8px
      line-height: 10px
      color: #AFB6BA
  .user 
    display: flex
    align-items: center
    border-radius: 16px
    background-color: rgba(15, 28, 53, 0.5)
    margin-right: 13px
    overflow: hidden
    &--tablet
      display: none
  /*.user__avatar 
    display: flex
    align-items: center
    justify-content: center
    margin-right: 8px
    width: 33px
    height: 33px
    cursor: pointer
    text-align: center
    font-size: 12px
    color: #FFFFFF
    border-radius: 33px
    background-color: #142443 */
  .user__mail 
    padding: 9px 26px 9px 17px
    font-weight: 700
    font-size: 12px
    line-height: 15px
    color: #ffffff
    background: url(../img/main/mail-bg.png)
    background-size: 100% 100%
    background-position: center
    background-repeat: no-repeat
    transition: all .3s linear
    &:hover
      opacity: 0.7
  .user__log-out 
    font-weight: 700
    font-size: 12px
    line-height: 15px
    color: #ffffff
    padding: 9px 17px 9px 4px
    transition: all .3s linear
    &:hover
      opacity: 0.7
  &__lang
    position: relative
    margin-right: 20px
    & .select-toggle
      display: flex
      position: relative
      font-size: 14px
      line-height: 17px
      font-family: 'Montserrat', sans-serif
      color: #fff
      background: none
      border: none
      outline: none
      padding-right: 20px
      &::after
        position: absolute
        content: ''
        transition: all 0.3s linear
        transform: rotateX(0deg) translateY(0px)
        width: 10px
        height: 100%
        right: 0
        background-size: contain
        background-image: url('../img/arrow-lang.svg')
        background-repeat: no-repeat
        background-position: center
      & .n-header__lang-full
        display: none
      & .n-header__lang-short
        position: relative
        top: 1px
      & .n-header__lang-img 
        margin-right: 6px
    & .select-dropdown .select-options
      flex-direction: column
      margin-right: 0
    & .select-dropdown .select-option
      display: flex
      align-items: center
      margin-right: 0
      font-size: 14px
      line-height: 17px
      font-family: 'Montserrat', sans-serif
      color: #142546
      cursor: pointer
      &:not(:last-of-type) 
        margin-bottom: 16px
    & .select-dropdown
      display: none
      background: #FCFEFF
      border: 1px solid #E8F5F3
      border-radius: 8px
      padding: 12px
      & .n-header__lang-short 
        display: none
      & .n-header__lang-img 
        width: 16px
        height: 16px
        margin-right: 8px
    &-select.select-show
      & .select-toggle::after
        transform: rotateX(180deg) translateY(2px)
      & .select-dropdown
        display: block
        position: absolute
        top: 100%
        left: 0
        z-index: 100
  &__login-link
    align-self: center
    color: #fff
    font-weight: 500
    font-size: 16px
    line-height: 1.5
    text-align: center
    color: #061429
    & a
      font-weight: 600
      color: #061429
@media screen and (max-width: 1200px)
  .n-header
    padding: 24px 0
    & .notification
      display: block
    &__main-link
      margin-right: auto
    .n-burger-body
      flex-direction: column
      position: fixed
      z-index: 10000
      top: 65px
      right: 20px
      background: #152442
      border: 1px solid rgba(208, 236, 255, 0.5)
      padding: 15px 20px
      width: 218px
      height: auto
      max-width: 100%
      box-sizing: border-box
      border-radius: 15px
      transform: translate(125%, 0)
      transition: .3s
      .n-burger-btn
        display: none
      &.open
        transform: translate(0)
        position: fixed
      nav
        margin: 0
        width: 100%
        order: 4
      ul
        flex-direction: column
        margin-right: 0
        width: 100%
        li
          margin-right: 0
          padding: 10px 0
          &:first-child
            padding-top: 0
          &.n-burger-item
            display: block
      & .user 
        display: none
      & .n-header__reg-btn
        margin: 25px 0 0
        order: 8
        width: 100%
        text-align: center
    &__lang 
      display: none
    &__auth-btn
      width: 100%
      margin: 0 0 20px
      text-align: center
    .n-header__auth-btn:first-child
      order: 1
    .n-header__auth-btn:last-child
      order: 2
    .notification
      margin-right: 0
    .user--tablet
      display: flex
    .n-header__reg-btn--tablet
      display: block
    &__theme-wrapper
      margin-top: 10px
      order: 5
      display: flex
      justify-content: space-between
      width: 100%
      p
        display: block
        font-size: 14px
        line-height: 24px
        font-weight: 600
        color: #FFFFFF
    &__theme-toggler
      cursor: pointer
      width: 24px
      height: 24px
      margin: 0
      padding-right: 25px
      & img
        width: 24px
        height: 24px
        object-fit: contain
      &.dark
        display: none
    .n-burger-btn
      display: block
      cursor: pointer
      background-image: url('../img/main/burger.svg')
      background-size: 24px
      background-position: center
      background-repeat: no-repeat
      width: 32px
      height: 32px
    .close-burger
      position: absolute
      top: 16px
      right: 20px
      color: #FFFFFF
@media screen and (max-width: 1023px)
  .n-header--mini
    .n-header__main-link 
      width: 84px
    .n-header__main-link img
      max-width: 100%
      display: none
    .n-header__main-link .logo-dark
      display: block
      margin-right: 10px
@media screen and (max-width: 991px)
  .n-header
    .container
      max-width: 100%
@media screen and (max-width: 768px)
    .n-burger-body
      max-height: calc( 100vh - 80px )
      overflow-y: scroll
      overflow-x: auto
@media screen and (max-width: 576px)
  .n-header
    &_auth-user
      display: block
      color: #FFFFFF
      margin: auto 12px  
      width: 33px
      height: 33px
      background: url('../img/main/userLight.svg') center no-repeat
      background-size: contain
    & .user--tablet 
      display: none
    .n-header__reg-btn--tablet
      display: none
    .n-burger-body
      min-height: 100vh
      width: 100%
      position: fixed
      border: 0
      border-radius: 0
      margin: 0
      top: 0
      left: 0
      padding: 100px 25px
      & nav 
        max-width: 280px 
        margin: 28px auto 0
        border-top: 2px solid rgba(228, 239, 255, 0.2)
        border-radius: 1px
        padding-top: 40px
        & li 
          padding: 15px 0
      & .n-header__theme-wrapper
        max-width: 280px 
        margin: 15px auto 0
      & .n-burger__lang
        & .select-dropdown
          width: 100%
          border-left: 0
          border-bottom: 0
          border-radius: 0
      & .n-header__reg-btn
        width: 100%
        max-width: 280px
        margin-top: 20px
        order: initial
      & .user 
        display: flex
        margin-right: 0
      & .close-burger 
        display: block
        background: none
        top: 22px
        right: 16px
    &__burger-logout
      display: block
      order: 6
      margin: 35px auto 0
      font-weight: 600
      font-size: 14px
      line-height: 1.2
      color: #ffffff
    &__burger-auth
      display: flex
      justify-content: center
      width: 100%
      margin-bottom: 30px
      order: 3
      color: #FFFFFF
      p
        line-height: 32px
        margin-left: 8px  
  .n-header--mini
    .n-header__login-link
      font-size: 12px
.light .n-header
  .n-header__auth-btn
    color:#14223d
    &:hover
      color: rgba(20, 37, 70, 0.5)
  .n-header_auth-user
      background: url('../img/main/user.svg') center no-repeat
      background-size: contain
  .n-header__reg-btn
    color: #ffffff
    background: linear-gradient(90deg, #101E38 0%, #142545 100%)
    &:hover
      color: rgba(255, 255, 255, 0.5)
  .notification-body__count
    color: #142546
  .notification-body__list .simplebar-scrollbar:before
    background: #DEF2FA
  .n-burger__notification
    color: #142546
  .notification-body__title
    color: #142546
  .notification-body__text
    color: #142546
  .notification-body__item:not(:first-child)
    border-top: 1px solid #eff5f8
  .notification-body
    background: #FCFEFF
  .notification-body__header
    background: #FCFEFF
    border-bottom: 1px solid #eff5f8
  .notification 
      background-image: url(../img/notification-icon.svg)
  .user 
    background-color: #FCFEFF
  .user__avatar 
    background-color: #CFE9FA
  .user__mail 
    color: #142546
  .user__log-out 
    color: #142546
  & .select-toggle
    color: #142546
  .n-burger-body.open
    background: #E3EFFF
    border: 1px solid rgba(20, 37, 68, 0.5)
  &  .n-burger-btn
    background-image: url('../img/main/burgerDark.svg')
  & .n-burger__lang
    color: #142546
    &-select
      flex: 1 0 auto
      text-align: right
      &.select-show .select-dropdown 
        transform: translate(0)
    & .select-toggle
      color: #142546
    & .select-dropdown-close
      & svg path
        fill: #304B57
    & .select-dropdown
      background: #ECF8F9
      border: 1px solid transparent
  & .n-burger-body .n-burger-btn
    @media screen and (max-width: 576px)
      background: none
      color: #131931
.n-header-snow
  margin: 0 26px 0 12px
  position: relative
  @media screen and (max-width: 1200px)
    order: 5
    margin-left: auto
    margin-right: 0
    margin-bottom: 10px
  &::before
    z-index: 10
    position: absolute
    content: ""
    background-image: url(../img/snowfall.svg)
    background-repeat: no-repeat
    background-position-y: center
    background-position-x: 5px 
    width: 44px
    height: 22px
    pointer-events: none
    display: none
  &.toogleOn::before
    display: block
.n-header-snow__toogle
  width: 44px
  height: 22px
  z-index: 0
  padding: 0
  appearance: none
  border: none
  cursor: pointer
  position: relative
  border-radius: 16px
  -moz-border-radius: 16px
  -webkit-border-radius: 16px
  transition: all 0.3s linear
  &:before
      content: ' '
      position: absolute
      left: 0px
      top: 0px
      width: 44px
      height: 22px
      background: #213151
      z-index: 1
      border-radius: 16px
      -moz-border-radius: 16px
      -webkit-border-radius: 16px
  &:after
      content: ' '
      height: 18px
      width: 18px
      border-radius: 50%
      background: #f1f1f1
      position: absolute
      z-index: 2
      top: 2px
      left: 2px
      transition: all 0.2s linear
  &.toogleOn
      &:before
          background: #40A7EA !important
      &:after
          transform: translateX(21px)